<template>
  <el-card class="box-card">
    <el-button type="primary" @click="$router.push('/admin/main/user/add-employee')">Add Employee</el-button>
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="username">
        <el-input placeholder="username" v-model="param.username"></el-input>
      </el-form-item>
      <el-form-item label="taxid">
        <el-input placeholder="taxid" @input="inputTaxid" v-model="param.taxid"></el-input>
      </el-form-item>
      <el-form-item label="hired">
        <el-date-picker value-format="yyyy-MM-dd" placeholder="hired" v-model="param.hired"></el-date-picker>
      </el-form-item>
      <el-form-item label="experience">
        <el-input placeholder="experience" v-model="param.experience"></el-input>
      </el-form-item>
      <el-form-item label="salary">
        <el-input placeholder="salary" v-model="param.salary"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="searchForm">Query</el-button>
      </el-form-item>
    </el-form>
    <el-table
        border
        ref="multipleTable"
        :data="tableList"
        tooltip-effect="dark"
        style="width: 100%"
    >
      <el-table-column
          align="center"
          label="username"
          prop="username"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          prop="taxid"
          label="taxid"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          prop="hired"
          label="hired"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          prop="experience"
          label="experience"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          prop="salary"
          label="salary"
          sortable
      >
      </el-table-column>
    </el-table>
    <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="changePage"
    >
    </el-pagination>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      param: {
        username: '',
        taxid: '',
        hired: '',
        experience: '',
        salary: '',
      },
      tableList: [],
      pageInfo: {
        pageNum: 1,
        pageSize: 10,
      },
      total: 0
    }
  },
  mounted() {
    this.getTableList(this.pageInfo)
  },
  methods: {
    getTableList() {
      this.$axios.post('/employees/findAll', Object.assign(this.param, this.pageInfo)).then(res => {
        this.tableList = res.data.rows;
        this.total = res.data.count;
      })
    },
    searchForm() {
      if (this.param.taxid) {
        let temp = this.param.taxid.replace(/[^0-9]/ig, "")
        if (temp.length !== 9) {
          this.$message.warning('The taxid must contain 9 digits')
          return
        }
      }
      this.pageInfo.pageNum = 1;
      this.getTableList();
    },
    changePage(e) {
      this.pageInfo.pageNum = e
      this.getTableList()
    },
    inputTaxid(e) {
      if ((e.length === 3 || e.length === 6) && e.charAt(e.length - 1) !== '-') {
        this.param.taxid = e + '-'
      }
    }
  },
}
</script>

<style scoped lang="scss">
.demo-form-inline, .el-pagination {
  margin-top: 10px;
}
</style>
